
@font-face{
    font-family: 'text';
    src: url('../fonts/JosefinSans-Light.ttf');
}

.conteiner{
    display: flex;
    position: fixed;
    justify-content: space-around;
    align-items: center;
    width: 100vw;
    height: 20vh;
    padding: 0 200px;
    z-index: 1000;
}

.conteiner__item{
/*     padding: 20px; */
    width: auto;
    height: auto;
    position: relative;

}

div img{
    width: 90px;
    padding: 20px;
}



.iniciomenu::after{
    display: block;
    position: absolute;
    text-align: center;
    padding-top: 30px;
    content: "inicio";
    font-family: 'text';
    font-size: 20px; 
    box-sizing: border-box;
    height: 90px;
    width: 90px;
    border-radius: 50%;
    color: BLACK;
    background-color: white;
    transform: scale(0) ;
    transition: all 300ms ease-in-out;
}
.iniciomenu:hover::after{
    transform: scale(1);
}


