@media only screen and (max-width: 600px){

    .conteiner{
        display: flex;
        position: fixed;
        justify-content: space-around;
        align-items: center;
        width: 100vw;
        height: 10vh;
        padding: 10px;
        z-index: 1000;
    }

    .conteiner__item{
        /*     padding: 20px; */
            width: 40px;
            height: 40px;
        
        }
        
        div img{
            width: 40px;
            padding: 0px;
        }

        .iniciomenu::after, .que::after, .como::after, .contacto::after{

            padding-top: 2px;
            font-size: 15px; 
            height: 20px;
            width: 20px;
            padding-left: 5px;
        }
        .contacto::after{
            content: 'con' + '<br/>' ;
        }
}