*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: pointer;
}
h2{
  font-weight: normal;
}

:root{
  --cyan: #28a5a2;
  --pink: #ed1456;
}
.header{
  display: grid;
  grid-template: "header__ubi header__media" 100vh / 1fr 1fr;
}

@font-face{
  font-family: "ubi";
  src: url('../fonts/Dance.ttf')
}

@font-face{
  font-family: "media";
  src: url('../fonts/LABTSEC_.ttf')
}
@font-face{
  font-family: 'text';
  src: url('../fonts/JosefinSans-Light.ttf');
}


.header__ubi{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-family: 'ubi';
  color: white;
  background-color: var(--cyan);
}
.header__ubi__tittle{
  transform: rotateX(270deg);
  transition-delay: all 2000ms;
  transition: ease-in all 500ms;
  animation: intro 500ms ease-in-out;
  animation-delay: 2000ms;
  animation-fill-mode: forwards;
  font-size: 100px;
  padding: 150px 0 150px 150px;
  background-image: url('../img/textura1.png');
  background-size: cover;
  position: fixed;
}

@keyframes intro {
  from {
    transform: rotateX(270deg);
  }
  to {
    transform: rotateX(360deg);
  }
}

/* :hover.header__ubi, :hover.header__media{
  letter-spacing: 40px;
} */


.header__media{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-family: 'media';
  font-weight: normal;
  color: white;
  width: 50vw;
  background-color: var(--pink);
  z-index: -100;
  animation: index linear 10ms ;
  animation-delay: 3500ms;
  animation-fill-mode: forwards;
}

@keyframes index {
  from {
    z-index: -100;
  }
  to {
    z-index: 0;
    
  }
}
.header__media__tittle{
  transform: translateX(-700px);
  transition-delay: all 2000ms;
  transition: ease-in all 500ms;
  animation: intro2 1000ms ease-in-out;
  animation-delay: 2000ms;
  animation-fill-mode: forwards; 
  font-size: 150px;
  padding: 125px 100px 125px 0;
  border-top: 1px white solid;
  border-right: 1px white solid;
  border-bottom: 1px white solid;
  position: fixed;
}

@keyframes intro2 {
  from {
    transform: translateX(-700px);
  }
  to {
    transform: translateX(0);
  
  }
}




.header__detail{
  position: absolute;
  bottom: 40px;
  right: -45px;
  height: 90px;
  animation: introdetail  2500ms ease-in-out, App-logo-spin infinite 1000ms ease-in-out;
  animation-fill-mode: forwards;
  z-index: 100;
}
  @keyframes introdetail {
  0%{
    bottom: 330px;
  }
  60%{
    bottom: 330px;
  }
  100% {
    bottom: 40px;
  } }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }}


.header__detail2{
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 100px;
}

.btn{
  background-color: transparent;
  color: white;
  font-family: 'text';
  font-size: 15px;
  padding: 15px;
  border: 1px white solid;

}

.hidetext{
  opacity: 0;
}


@media only screen and (max-width: 600px) {
  .header{
    display: grid;
    grid-template: "header__ubi header__media" 100vh / 50% 50%;
  }

  .header__ubi{
    font-size: 20px;
  }
  .header__media{
    font-size: 30px;
  }

  .header__detail{
  margin-left: -10px;
  }

  .header__detail2{
    margin-right: -10px;
    }
  
}