
:root{
    --cyan: #28a5a2;
    --pink: #ed1456;
    --black: #1c1c1c;
  }
  
  
  @font-face{
    font-family: 'text';
    src: url('../fonts/JosefinSans-Light.ttf');
  }

.llamada{
    display: grid;
    grid-template: "llamada__1 llamada_2"  / 1fr 1fr;
    background-color: var(--pink);
    
    z-index: 50;
    align-items: center;
    justify-content: center;
}

.llamada__fondo{
    position: relative;
    width: 300px;
    height: 300px;
    margin: 0px auto;
    padding-top: 20%;
    border-radius: 50%;
    background-color: var(--pink);

}
.llamada__1{
    padding: 150px 100px;
}
.llamada__2{
    padding: 150px 100px;
    background-image: url('../img/textura2.png');
    background-size: cover;
}

.llamada__tittle{
    font-size: 30px;
    font-weight: bold;
    font-family: 'text';
    color: white;
    z-index: 100;
}

.llamada__desc{
    font-family: 'text';
    font-size: 20px;
    color: white;
    z-index: 100;
}

.llamada__tel{
    text-align: center;
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    font-family: 'text';
    color: white; 
    z-index: 100;
}

.llamada__mail{
    text-align: center;
    padding: 10px;
    font-family: 'text';
    font-size: 20px;
    color: white;
    z-index: 100;

}