
@font-face{
    font-family: 'text';
    src: url('../fonts/JosefinSans-Light.ttf');
}
@font-face{
    font-family: 'frase';
    src: url('../fonts/Dance.ttf');
}
:root{
    --cyan: #28a5a2;
    --pink: #ed1456;
    --black: #1c1c1c;
  }


.conteinerque{
/*     height: 200vh; */
    color: #1c1c1c;
    background-color: #1c1c1c;
    background-image: url('../img/textura2.png');
    background-size: cover;
    padding-top: 10px;
    padding-bottom: 150px;
    display: grid;
    grid-template: "frase frase2" auto
                    "conteinerque__item1 img1" 300px  
                  "img2 conteinerque__item2" 300px  
                  "conteinerque__item3 img3" 300px  
                  "img4 conteinerque__item4" 300px / 1fr 1fr ;
    z-index: -100;
    animation: background infinite 8000ms linear;
}
@keyframes background {
    0% {
        background-color: var(--black);
    }
    33% {
        background-color: var(--cyan);
    }
    66%{
        background-color: var(--pink);
    }
    100% {
        background-color: var(--black);
    }
  }

  .frase{
      font-family: 'frase';
      font-weight: bold;
      font-size: 10vh;
      color: white;
      padding: 50px;
      line-height: 10vh;
      
  }

.frase2{
    font-family: 'frase';
    font-weight: bold;
    font-size: 10vh;
    color: var(--cyan);
    padding: 50px;
    line-height: 10vh;
    mix-blend-mode: multiply;
}
.conteinerque__item1, .conteinerque__item2, .conteinerque__item3, .conteinerque__item4{
    font-family: 'text';
    text-align: center;
    padding: 50px 200px;

    position: relative;
    z-index: 100;
}
.conteinerque__item__button{
    margin: 30px;
}
.conteinerque__item__title{
    margin-top: 10%;
}
.img1, .img2, .img3, .img4{
    text-align: center;
    padding: 40px;
}

.imgtext{
    width: 30%;
}

.conteiner__item1__fondo, .conteiner__item2__fondo, .conteiner__item3__fondo, .conteiner__item4__fondo{
    position: absolute;
    width: 0px;
    height: 0px;
    background-color: white;
    border-radius: 50%;
    left: 50%;
    bottom: 40%; 
    z-index: -50;
    /*     max-width: 400px;
    max-height: 400px; */
    transition-delay: 500ms;
    transition: all 800ms ease-in-out;
}



.showtext{
    transform: translatex(-50%) translateY(50%);
    /* left: 20%; */
    /* bottom: -43%;  */
    width: 400px;
    height: 400px;
}
