@media only screen and (max-width: 600px){
    .conteinerque{
        /*     height: 200vh; */

            grid-template: "conteinerque__item1" 300px  
                          "conteinerque__item2" 300px  
                          "conteinerque__item3 " 300px  
                          "conteinerque__item4" 300px;
    }

    .conteinerque__item1, .conteinerque__item2, .conteinerque__item3, .conteinerque__item4{

        padding: 50px 10px;
        margin-top: auto;
    
    }
    
    .img1, .img2, .img3, .img4{
        display: none;
 
    }
}